import { useXFieldForInput, XFieldName, XFieldValidator } from '@speedlo/xform'
import { useObserver } from 'mobx-react-lite'
import React from 'react'

import { StyledFormInput } from '../../atoms/FormInputStyled'

interface IProps extends LegacyOmit<React.InputHTMLAttributes<never>, 'name'> {
  fieldName: XFieldName
  validate?: XFieldValidator<string>
  fullWidth?: boolean
  children?: never
  addHiddenLabelWithPlaceholderText?: boolean
}

export type FormInputProps = IProps

export const FormInput = React.forwardRef<HTMLInputElement, IProps>(
  (
    {
      fieldName,
      fullWidth,
      validate,
      addHiddenLabelWithPlaceholderText: withLabel = true,
      ...props
    },
    inputRef,
  ) => {
    const getInputProps = useFormInputProps({ fieldName, validate })
    return useObserver(() => (
      <>
        <StyledFormInput
          {...getInputProps()}
          inputFullWidth={fullWidth}
          ref={inputRef}
          {...props}
        />
        {withLabel && props.placeholder && (
          <label hidden={true} htmlFor={getInputProps().id}>
            {props.placeholder}
          </label>
        )}
      </>
    ))
  },
)

interface IHookOptions {
  fieldName: XFieldName
  validate?: XFieldValidator<string>
}

export function useFormInputProps({ fieldName, validate }: IHookOptions) {
  const [field, getInputProps] = useXFieldForInput<string>(fieldName, {
    validate,
  })
  return () => ({
    ...getInputProps(),
    invalid: field.touched && Boolean(field.error),
  })
}
